// This is the root reducer of the feature. It is used for:
//   1. Load reducers from each action in the feature and process them one by one.
//      Note that this part of code is mainly maintained by Rekit, you usually don't need to edit them.
//   2. Write cross-topic reducers. If a reducer is not bound to some specific action.
//      Then it could be written here.
// Learn more from the introduction of this approach:
// https://medium.com/@nate_wang/a-new-approach-for-managing-redux-actions-91c26ce8b5da.

import initialState from './initialState';
import { reducer as getSpecialtiesReducer } from './getSpecialties';
import { reducer as getFavoriteClinicsReducer } from './getFavoriteClinics';
import { reducer as getClinicsReducer } from './getClinics';
import { reducer as getClinicsBySpecialtyReducer } from './getClinicsBySpecialty';
import { reducer as sendReferralReducer } from './sendReferral';
import { reducer as getReferralPatientReducer } from './getReferralPatient';
import { reducer as saveReferralNoteReducer } from './saveReferralNote';
import { reducer as getReferralNoteReducer } from './getReferralNote';
import { reducer as updateReferralReducer } from './updateReferral';

const reducers = [
  getSpecialtiesReducer,
  getFavoriteClinicsReducer,
  getClinicsReducer,
  getClinicsBySpecialtyReducer,
  sendReferralReducer,
  getReferralPatientReducer,
  saveReferralNoteReducer,
  getReferralNoteReducer,
  updateReferralReducer,
];

export default function reducer(state = initialState, action) {
  let newState;
  switch (action.type) {
    // Handle cross-topic actions here
    default:
      newState = state;
      break;
  }
  return reducers.reduce((s, r) => r(s, action), newState);
}
